.backdrop
  position: fixed
  top: 0
  left: 0
  width: 100%
  height: 100vh
  background: rgba(0, 0, 0, 0.6)
  z-index: 1

.modal
  border: none
  border-radius: 0.3rem
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2)
  position: absolute
  top: 50%
  left: 50%
  transform: translate(-50%, -50%)
  z-index: 1
